@charset 'UTF-8';
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');

* {margin:0; padding:0;}
ul,li {list-style:none;}
a {text-decoration:none; color:#fff;}
img {border:0; outline:0; vertical-align:bottom;}

body {background:#363636; color:#fff; font-size:16px; font-family: 'Noto Sans KR', sans-serif;}
#header {padding-top:20px;}
#footer {padding-bottom:20px;}

.wrapper {width:1280px; margin:0 auto; position:relative;}
.wrapper:after {content:''; clear:both; height:0; display:block;}
h1.logo {color:#df291c; width:160px; height:75px; background-image:url('../img/logo.png'); background-size:contain; background-position: 0 0; text-indent:-9999px;}
#gnb > li {float:left; margin-top:20px;}
#gnb > li > a {padding:10px 20px; font-weight:bold}
#gnb > li:first-child > a {padding-left:0;}
#gnb > li > a:hover {color:#df291c}

/*search*/
.search {position:absolute; top:0; right:0; width:200px;}
.search input {width:100%; background:transparent; border:0; height:40x; border-bottom:2px solid #df291c; line-height: 40px; outline:0; color:#fff; font-size:16px;}
.search .s-btn {position:absolute; top:0; right:0; font-size:20px; top:50%; margin-top:-10px; cursor:pointer; color:#df291c;}

/*content*/
#content {padding:50px 0;}
#content:after {content:''; claer:both; height:0;}

.movieBox {float:left; width:50%; color:#363636;padding:0 10px; margin-top:150px; box-sizing:border-box; cursor:pointer;}
.movieBox > .inner {background-color: #fff; padding:30px; border-radius: 30px;  box-shadow: 3px 3px 3px rgba(0,0,0,0.3);}
.movieBox > .inner:after {content:''; clear:both; height:0; display:block;}
.info {width:60%; float:right; }
.desc {margin-top:20px;}
.genres {margin-top:20px;}
.genres > span {display:inline-block; margin-right:10px;}